<template>
    <div id="popup">
        <h1 @click="update">hello</h1>
    </div>
</template>

<script>
    import store from './store.js'

    export default {
        name: 'popup',
        data() {
            return {
                greeting: 'hi'
            }
        },
        methods: {
            update () {
                store.update()
            }
        }
    }
</script>

<style>
    body {
        margin: 0
    }
    #popup {
        width: 200px;
        border-right: 1px solid blue;
    }
</style>


