import Vue from 'vue'

export default {
    state: {
        subscribed: [],
        itemQueue: [],
        notiEnabled: null,
        badgeEnabled: null
    },
    update() {
        window.chrome.storage.sync.get(Object.keys(this.state), x => {
            Object.keys(x).map(k => {
                console.log(k)
                console.log(this.state)
                Vue.set(this.state, k, x[k])
            })
            console.log(this.state)
        })
    }
}


